@import url(https://fonts.googleapis.com/css?family=Lexend+Deca&display=swap);
:root {
  --main-font: 'Lexend Deca';
}

body {
  margin: 0;
  font-family: 'Lexend Deca', sans-serif;
  font-family: var(--main-font), sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Lexend Deca', sans-serif;
  font-family: var(--main-font), sans-serif;
}

input {
  font-family: 'Lexend Deca', sans-serif;
  font-family: var(--main-font), sans-serif;
}

button {
  font-family: 'Lexend Deca', sans-serif;
  font-family: var(--main-font), sans-serif;
}

.module-container {
  margin: auto;
  width: 80%;
}

.disabled-container {
  display: none;
}

:root {
  --main-color: crimson;
  --secondary-color: rgb(161, 10, 40);
  --tertiary-color: crimson;
  --quaternary-color: crimson;
  --text-color: white;
}

.header-container {
  background: var(--main-color);
  color: var(--text-color);
  margin: 0 0 1em;
  width: 100%;
}

.header-container h1 {
  margin: 0;
  padding: .5em;
}

.header-nav {
  padding: 0;
  background: var(--secondary-color);
}

.header-nav ul {
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.header-nav ul li {
  margin: 0;
  padding: 1em;
  display: block;
  cursor: pointer;
}

.header-nav ul li:hover {
  background: var(--main-color);
}

.header-nav ul li.active {
  background: var(--main-color);
}

.spell-list-header ul {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  list-style-type: none;
}

.spell-list-header ul li {
  margin: 0;
  padding: 1em 0;
  width: 7em;
  cursor: pointer;
}

.spell-list-header ul li:first-child {
  text-align: center;
}

:root {
  --header-bg: var(--main-color);
  --header-text: var(--text-color);
}

.spell-container {
  margin: 0 0 0.5em;
}

.spell-header {
  display: flex;
  justify-content: space-between;
  background: var(--main-color);
  background: var(--header-bg);
  color: var(--text-color);
  color: var(--header-text);
  cursor: pointer;
}

.spell-header ul {
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  list-style-type: none;
}

.spell-header ul li {
  padding: 1em 0;
}

.spell-header ul li.spell-header-level {
  width: 112px;
  text-align: center;
}

.spell-header ul li.spell-header-name {
  width: calc(100% - 224px);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.spell-header ul li.toggle {
  width: 112px;
  margin-left: auto;
  text-align: center;
}

.spell-desc {
  padding: 1em;
}

p {
  margin: 0;
}

p.type {
  margin: 0 0 1em;
  font-style: italic;
}

.main {
  margin: 0;
}

.descriptor {
  font-weight: bold;
}

.hidden {
  display: none;
}

:root {
  --form-button: var(--main-color);
  --form-button-text: var(--text-color);
}

.spell-form {
  width: 100%;
}

.spell-form p.spell-show-filter {
  margin: 1em 0;
  text-align: center;
}

.spell-form p.spell-show-filter:hover {
  color: var(--main-color);
  cursor: pointer;
}

.spell-form input {
  width: -webkit-fill-available;
  width: -moz-available;
  width: stretch;
  margin: 0;
  padding: .9em 1em;
}

.disable-filter {
  display: none;
}

form.adv-filter .check-filter {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-flow: row wrap;
}

form.adv-filter .check-filter div {
  margin: 0.5em 0.5em 0;
  padding: 1em;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

form.adv-filter .check-filter div.active {
  background: var(--secondary-color);
  color: var(--text-color);
}

form.adv-filter button {
  background: var(--form-button);
  color: var(--form-button-text);
  border: none;
  cursor: pointer;
  padding: 1em 2em;
  margin: 1em 0;
  width: 100%;
}


